/*ფონტების შემოტანა*/

/*სეტაპი*/
html{
  height: 100%;
}
body{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  height: 100%;
  align-items: center;
  background-color: rgb(72, 155, 247);
  justify-content: center;
}
#root{
  display: flex;
  grid-area: 1/2/1/2;
  justify-content: center;
  height: 70%;
  align-items: center;
}

/*პაროლის გენერატორის ჩარჩო*/
