/*რეინჯის დიზაინი*/
input[type='range']{
  -webkit-appearance: none;
  width: 100%;
  height: 2px;
  outline: none;
  background: rgb(110, 112, 129);
  background-image: linear-gradient(#1900ff, #1900ff);
  background-repeat: no-repeat;
  background-size: 0% 100%;
}
input[type='range']::-webkit-slider-runnable-track{
  -webkit-appearance: none;
  height: 2px;
  box-shadow: none;
  border: none;
}
input[type=”range”]::-moz-range-track{
  -webkit-appearance: none;
  height: 2px;
  box-shadow: none;
  border: none;
}
input[type=”range”]::-ms-track{
  -webkit-appearance: none;
  height: 2px;
  box-shadow: none;
  border: none;
}
input[type='range']::-webkit-slider-thumb{
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: white;
  position: relative;
  bottom: 9px;
}
input[type=”range”]::-moz-range-thumb{
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: white;
  position: relative;
  bottom: 9px;
}
input[type=”range”]::-ms-thumb{
  -webkit-appearance: none;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background: white;
  position: relative;
  bottom: 9px;
}

